<script>
import listNoData from '@/components/common/list-no-data'
import AdLoop from '../home/adList'
import ListLoading from '@/components/movie/common/list-loading'
import adView from '@/components/mutiAdList/index'
export default {
  name: 'header-nav',
  components: {
    listNoData,
    AdLoop,
    ListLoading,
    adView
  },
  props: {
    list: {
      type: Array,     // 
      default: null
    },
    article_check: {   // 
      type: Boolean,
      default: false
    },
    deleteBol: {       // 
      type: Boolean,
      default: false
    },
    readLineBol: {
      type: Boolean,
      default: true
    },
    collectionBtn: {
      type: Boolean,
      default: false
    },
    gray: {             // 
      type: Boolean,
      default: true
    },
    isHistory: {
      type: Number,
      default: 0
    },
    column_id: {}, // id 
    hasBottom: {
      type: Number
    },
    loading: {
      type: Number
    }
  },
  data () {
    return {

    }
  },
  mounted () {
    
  },
  computed: {
    dbClick () {
      return this.$store.state.common.dbClick
    },
    channel_id () {
      return this.$store.state.common.columnid
    }
  },
  watch: {
    dbClick () {
      if (this.column_id * 1 === this.channel_id * 1) {
        this.refresh()
      }
    }
  },
  methods: {
    /**
     * @method 
     * @param {string} id id
     */
    // 
    scrollFn () {
      // 
      let ads = this.$refs.newlistAdbox.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    deleteFn (id, bol, index) {
      this.$emit('deleteFn', id, bol, index)
    },
    closeAd (index) {
      this.$emit('deleteAd', index)
    },
    // 
    refresh () {
      this.$emit('refresh')
    },
    // 
    toPage (item, index) {
      if (item.type === 5) {
        this.$toast({
          message: '',
          position: 'middle',
          duration: 1500
        })
        return
      }
      this.$emit('isgoblack')               // 
      if (this.$route.query.inviteCode) {
        this.$router.push({
          path: '/ArticleDetails/' + item.id,
          query: this.$route.query
        })
      } else {
        this.$router.push('/ArticleDetails/' + item.id)
      }
      this.$emit('upDate', index)          // 
      this.setHistory(item)
    },
    setHistory (item) {
      if (item.type === 100 || item.type === 'ad') {
        return
      }
      this.$utils.cache('history', null)
      let arr = []
      if (this.$utils.cache('historys')) {
        arr = JSON.parse(this.$utils.cache('historys'))
      }
      arr = arr.length ? arr : []
      for (let i in arr) {
        if (item.id === arr[i].id) {
          arr.splice(i, 1)
        }
      }
      if (arr.length === 200) {
        arr.splice(199, 1)
      }
      arr = [item, ...arr]
      this.$utils.cache('historys', JSON.stringify(arr))
    },
    // 
    checkFn (item, index) {
      if (item.type === 'ad' || item.type === 100) {
        return
      }
      if (item.type === 'movie') {
        this.$router.push('/film/detail/' + item.item.vod_id)
      } else if (item.type === 'article') {
        this.$api.get('CHECK', {aid: item.item.id}, (res) => {
          if (item.item.type === 5) {
            this.$router.push('/notFound')
            return
          }
          this.$emit('isgoblack')               // 
          if (this.$route.query.inviteCode) {
            this.$router.push({
              path: '/ArticleDetails/' + item.item.id,
              query: this.$route.query
            })
          } else {
            this.$router.push('/ArticleDetails/' + item.item.id)
          }
          this.$emit('upDate', index)          // 
          this.setHistory(item.item)
        })
      } else {
        if (item.redirect_url && item.redirect_url.indexOf('#') > -1) {
          item.redirect_url = item.redirect_url
        } else if (item.redirect_url && item.redirect_url.indexOf('http') > -1) {
          this.$router.push('/browser/' + item.id)
          return
        }
        if (this.article_check) {
          this.$api.get('CHECK', {aid: item.id}, (res) => {
            if (item.redirect_url) {
              this.$router.push(item.redirect_url)
            } else {
              this.toPage(item, index)
            }
          })
        } else {
          if (item.redirect_url) {
            this.$router.push(item.redirect_url.replace('#', ''))
          } else {
            this.toPage(item, index)
          }
        }
      }
    }
  }
}
</script>

