          background: #f5f5f5;
        }
      }
      &.adDetail {
        padding: 0!important;
        margin-top: -4px;
        &::before {
          display: none;
        }
        &::after{
          display: none;
        }
      }
      .img-big{
        .imgList{
          margin-top: 20px;
          width: 100%;
          .imgDiv{
            width: 100%;
            max-height: 360px;
            overflow: hidden;
            border-radius: 16px;
            img{
              width: 100%;
              border-radius: 16px;
            }
          }
        }
      }
      .img-three{
        .imgList{
          padding-top: 40px;
          .imgDiv{
            width: 225px;
            height: 140px;
            border-radius: 16px;
            overflow: hidden;
            background-size: 120%!important;
            img{
              width: 100%;
              height: 100%;
              border-radius: 16px;
            }
          }
          .imgDiv:nth-of-type(1){
            margin-right: 4px;
          }
          .imgDiv:nth-of-type(2){
            margin-right: 4px;
          }
        }
      }
      .img-one{
        .img{
          width: 200px;
          height: 124.4px;
          margin-left: 10px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .title{
        font-size: 36px;
        line-height: 50px;
        max-height: 150px;
        overflow: hidden;
      }
      .address,.num{
        margin-right: 20px;
        font-size: 24px;
        line-height: 26px;
        max-width: 180px;
        color: #999;
      }
      .time{
        margin-right: 20px;
        font-size: 24px;
        line-height: 26px;
        color: #999;
      }
      .label-txt{
        height: 26px;
        font-size: 20px;
        line-height: 26px;
        margin-right: 10px;
        color: #ED575E;
        box-sizing: border-box;
        // border: 1px solid #ED575E;
        border-radius: 12px;
      }
      .expired,.delete{
        color: #ED535E;
        line-height: 30px;
        padding-left: 15px;
        padding-right: 15px;
        border: #ED535E 1px solid;
        text-align: center;border-radius: 15px;
        margin-left: 10px;
      }
      .delete{
        color: #f4a82b;
        border: 1px solid #f4a82b;
        padding: 4px 20px 6px;
        line-height: 34px;
        border-radius: 44px;
      }
      .label-top{
        background: url('../../assets/img/index_top.png') no-repeat;
        background-size: 100% 100%;
        width: 46px;
        height: 22px;
      }
      .label-hot{
        background: url('../../assets/img/index_hot.png') no-repeat;
        background-size: 100% 100%;
        width: 46px;
        height: 22px;
      }
      padding: 32px 0;
      margin: 0 30px;
      min-height: 84px;
      -webkit-transition: all 1s ease-in-out;
    }
  }
  .list-item {
    display: flex;
    flex-flow: row nowrap;
    padding-bottom: 60px;
    position: relative;
    .left-cover {
      width: 189px;
      height: 264px;
      overflow: hidden;
      margin-right: 26px;
      img {
        display: block;
        width: 100%;
      }
    }
    .rightContent {
      flex: 1;
      color: #666;
      min-width: 0;
      position: relative;
      .title {
        font-size: 32px;
        color: #232323;
        font-weight: 900;
