<template>
  <div class="final_confirm" @click.stop="">
    <div class="special"></div>
    <div class="confirmBox confirmBox1" v-if="maxIndex === activeIndex + 1">
      <div class="content">
        <span>{{list[activeIndex].nickname}}</span><br>
        <span class="redColor">{{$utils.thousandRead(list[activeIndex].vcoin, 2)}}</span>
      </div>
      <div class="c-tip"></div>
    </div>
    <div class="confirmBox confirmBox2" v-else>
      <div class="content">
        {{list[activeIndex].nickname}}<br>
        <span class="redColor"></span><br>
        
      </div>
    </div>
    <div class="bottom top-1px">
      <div class="cancel right-1px" @click="confirm('cancel')"></div>
      <div class="confirm" @click="confirm('confirm')"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array
    },
    activeIndex: {
      type: Number
    },
    maxIndex: {
      type: Number
    }
  },
  data () {
    return {

    }
  },
  methods: {
    confirm (e) {
      this.$emit('confirm', e)
    }
  }
}
</script>
<style lang="scss" scoped>
.final_confirm {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 665px;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  padding-top: 126px;
  .redColor {
    color: #F0193E;
  }
  .special {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -151px;
    width: 409px;
    height: 250px;
    background: url('../../assets/img/task/special_tip.png') no-repeat left top;
    background-size: 100% 100%;
  }
  .confirmBox {
    text-align: center;
    .content {
      line-height: 1.3;
      font-weight: 900;
      margin: 0 auto 46px;
      color: #666;
    }
    .c-tip {
      font-size: 30px;
      margin: 46px 0 71px;
      font-weight: 900;
    }
  }
  .bottom {
    display: flex;
    flex-flow: row nowrap;
    font-size: 30px;
    font-weight: 900;
    > div {
      box-sizing: border-box;
      width: 50%;
      height: 100px;
      text-align: center;
      line-height: 100px;
    }
    .confirm {
      color: #9B9B9B;
    }
  }
}
</style>



