<template>
  <div id="bindPhone">
    <div class="bindPhone">
      <div class="bgIcon" @click="close"></div>
      <div class="flex-row title">{{phoneParams.title ? phoneParams.title: ''}}</div>
      <div class="input-box flex-row">
        <input class="flex1" v-model="phone" placeholder="" pattern="\d*" type="number">
      </div>
      <div class="input-box flex-row" v-if="showCode">
        <input class="flex1" placeholder="" maxlength="6" v-model="captcha_code" type="text">
        <div class="captcha_code iconSize">
          <img :src="origin + timestamp" @click="timestamp = new Date().getTime()" alt="">
        </div>
      </div>
      <div class="input-box flex-row">
        <input class="flex1" placeholder="" pattern="\d*" v-model="code" type="number">
        <div class="button" @click='getCode' :class="{active: !phone, count: count > 0}">{{count > 0 ? `${count}s`: ''}}</div>
      </div>
      <div class="confirm flex-row" @click="confirm()" :class="{active: phone.length > 0 && code.length > 0}">{{phoneParams.confirm ? phoneParams.confirm: ''}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    phoneParams: {
      type: Object
    },
    errChange: {
      type: Number,
      default: 0
    }
  },
  data () {
    this.origin = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      phone: '', // 
      code: '', // 
      captcha_code: '',
      count: 0, // 
      isGetCode: true, // 
      showCode: false, // 
      timestamp: new Date().getTime()
    }
  },
  methods: {
    close () {
      this.$emit('closeBindPhone')
    },
    getCode () {
      if (this.isGetCode) {
        if (!this.$utils.isReg('PHONE_NUMBER', this.phone)) {
          return this.$toast({
            message: '',
            duration: 1500
          })
        }
        if (this.timer) {
          clearInterval(this.timer)
          this.timer = null
        }
        this.isGetCode = false
        let data = {
          mobile: this.phone
        }
        if (this.showCode) {
          data.captcha_code = this.captcha_code
        }
        this.$api.post('SMSCODE', data, res => {
          this.count = 60
          this.timer = setInterval(() => {
            if (this.count <= 0) {
              this.isGetCode = true
              clearInterval(this.timer)
              this.timer = null
            }
            this.count--
          }, 1000)
        }, res => {
          if (res.code !== 0) {
            this.isGetCode = true
            if (this.timer) {
              clearInterval(this.timer)
              this.timer = null
            }
            if (res.code === 10) {
              this.showCode = true
            }
          }
        })
      }
    },
    confirm () {
      if (!this.phone || !this.code) {
        return
      }
      if (this.showCode && this.captcha_code === '') {
        return this.$toast({
          message: '',
          duration: 1500
        })
      }
      if (this.$utils.isReg('PHONE_NUMBER', this.phone)) {
        let data = {
          mobile: this.phone,
          code: this.code
        }
        if (this.showCode) {
          data.captcha_code = this.captcha_code
        }
        this.$emit('closeBindPhone', data)
      } else if (this.showCode && this.captcha_code === '') {

      } else {
        return this.$toast({
          message: ''
        })
      }
    },
    setInput () {
      if (this.phone) {
        this.phone = this.phone.replace(/[^0-9]/g, '')
        if (this.phone.length > 11) {
          this.phone = this.phone.substr(0, 11)
        }
      }
      if (this.code) {
        this.code = this.code.replace(/[^0-9]/g, '')
        if (this.code.length > 11) {
          this.code = this.code.substr(0, 4)
        }
      }
    }
  },
  beforeUpdate () {
    this.setInput()
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  },
  watch: {
    errChange: function (a) {
      this.showCode = true
      this.timestamp = new Date().getTime()
    }
  }
}
</script>

<style lang="scss" scoped>
#bindPhone{
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  height: 100%;
  font-size: 30px;
  background: rgba(0, 0, 0, .5);
  color: #232323;
  z-index: 15;
  .bindPhone{
    padding: 0 46px;
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(135,134,134,0.50);
    border-radius: 10px;
    padding-bottom: 50px;
    width: 616px;
    .bgIcon {
      background: url('../../assets/img/task/close_gray.png');
      width: 26px;
      height: 26px;
      background-size: 100% 100%;
      position: absolute;
      right: 20px;
      top: 20px;
    }
    .title{
      height: 120px;
    }
    .input-box{
      height: 90px;
      border: 1px solid #DBDBDB;
      border-radius: 10px;
      margin-bottom: 40px;
      display: flex;
      flex-flow: row nowrap;
      input{
        padding: 0 10px;
        box-sizing: border-box;
        width: auto;
        min-width: 0;
        flex:1;
      }
      .captcha_code{
        width: 200px;
        height: 62px;
        margin-right: 10px;
      }
      .button{
        width: 225px;
        border-left: 1px solid #dbdbdb;
        height: 100%;
        color: #FF951B;
        line-height: 90px;
      }
      // .button.active{
      //   color: #232323;
      // }
      .count {
        color: #232323
      }
    }
    .confirm{
      height: 90px;
      border-radius: 45px;
      // color:#666;
      // background: #d8d8d8;
      background: #FF951B;
      color: #fff;
    }
    .confirm.active{
      background: #FF951B;
      color: #fff;
    }
  }
}
</style>



