<template>
  <div class="downloadApp flex-row" @click="toDownload" v-if="$config.source_style < 6">
    <div class="leftIcon iconSize">
      <img src="../../assets/img/logo.png" alt="logo">
    </div>
    <div class="center">
      <div class="d-title">APP</div>
      <div class="d-text"></div>
    </div>
    <div class="rightButton"></div>
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    toDownload () {
      window.open(this.$config.downloadUrl)
    }
  }
}
</script>
<style lang="scss" scoped>
.downloadApp {
  position: fixed;
  bottom: 100px;
  background: #fff;
  height: 130px;
  width: 100%;
  border-top: 1px solid #eee;
  box-sizing: border-box;
  justify-content: flex-start;
  padding: 0 48px 10px;
  .leftIcon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 15px;
  }
  .center {
    text-align: left;
    flex: 1;
  }
  .d-title {
    font-size: 27px;
    color: #666;
  }
  .d-text {
    font-size: 24px;
    margin-top: 10px;
    color: #999;
  }
  .rightButton {
    background-image: linear-gradient(62deg, #FABF00 0%, #F4A82B 100%);
    border-radius: 22.5px;
    width: 140px;
    height: 45px;
    font-weight: 900;
    text-align: center;
    line-height: 46px;
    color: #fff;
    font-size: 24px;
  }
}
</style>



