<template>
  <div id="chooseAccount" class="maskBox" @click="cancel">
    <div class="chooseAccount"  v-show="showChoose" @click.stop="">
      <div class="title"><span class="redColor"> </span></div>
      <div class="sec-title"><span class="redColor"></span></div>
      <div class="chooseList">
        <div class="chooseItem flex-row jusB" @click="setIndex(index)" v-for="(item, index) in list" :key="index">
          <div class="iconSize iconType" :class="`iconType${item.type}`"></div>
          <div class="centerContent">
            <div class="nickname">{{item.nickname}}</div>
            <div class="vcoin">{{$utils.thousandRead(item.vcoin, 2)}}</div>
          </div>
          <div class="selectIcon iconSize">
            <img src="../../assets/img/task/choose_select_active.png" v-if="item.select" alt="">
            <img src="../../assets/img/task/choose_select.png" v-else alt="">
          </div>
        </div>
      </div>
      <div class="buttonGroup">
        <div class="cancel" @click="cancel"></div>
        <div class="confirm" @click="confirm" :class="{active: isSelect}"></div>
      </div>
    </div>
    <final-confirm v-if="showFinalConfirm" :maxIndex="maxIndex" :activeIndex="activeIndex" :list="list" @confirm="confirmPost"></final-confirm>
  </div>
</template>
<script>
import FinalConfirm from './final_confirm'
export default {
  components: {
    FinalConfirm
  },
  data () {
    return {
      isSelect: false,
      showFinalConfirm: false,
      showChoose: true,
      list: [{
        // nickname: '',
        type: 1
      }, {
        // nickname: '3128',
        type: 2
      }],
      activeIndex: '',
      maxIndex: 0  // 1 2
    }
  },
  props: {
    config: {
      type: Object
    }
  },
  methods: {
    setIndex (index) {
      for (let i of this.list.keys()) {
        if (i === index) {
          this.$set(this.list[i], 'select', true)
          this.isSelect = true
          this.activeIndex = i
        } else {
          this.$set(this.list[i], 'select', false)
        }
      }
    },
    // 
    confirm () {
      if (this.isSelect) {
        this.showFinalConfirm = true
        this.showChoose = false
      }
    },
    // 
    confirmPost (e) {
      if (e === 'cancel') {
        this.showChoose = true
        this.showFinalConfirm = false
      } else if (e === 'confirm') {
        this.$emit('closeChoose', {
          action: 'confirm',
          type: this.list[this.activeIndex].type
        })
      }
    },
    cancel () {
      this.$emit('closeChoose', {
        action: 'cancel'
      })
    }
  },
  mounted () {
    this.$set(this.list[0], 'nickname', this.config.wechat_nickname)
    this.$set(this.list[0], 'vcoin', this.config.wechat_vcoin)
    this.$set(this.list[1], 'nickname', this.config.mobile_nickname)
    this.$set(this.list[1], 'vcoin', this.config.mobile_vcoin)
    if (this.config.wechat_vcoin * 1 > this.config.mobile_vcoin * 1) {
      this.maxIndex = 1
      this.setIndex(0)
    } else {
      this.setIndex(1)
      this.maxIndex = 2
    }
  }
}
</script>
<style lang="scss" scoped>
#chooseAccount {
  height: 100%;
  .chooseAccount {
    background: #FFFFFF;
    border-radius: 10px;
    width: 665px;
    font-size: 28px;
    text-align: center;
    font-weight: 900;
    .redColor {
      color: #F0193E;
    }
    .title {
      padding: 56px 62px 18px;
      font-size: 30px;
      line-height: 1.3;
      white-space: nowrap;
      font-weight: 900;
    }
    .sec-title {
      font-size: 900;
      padding-bottom: 36px;
    }
    .chooseList {
      .chooseItem {
        background: #F5F5F5;
        border-radius: 30px;
        width: 542px;
        margin: 0 auto;
        height: 98px;
        padding: 0 20px 0 54px;
        box-sizing: border-box;
        margin-bottom: 38px;
        .centerContent {
          display: flex;
          flex-flow: column wrap;
          padding: 0 10px;
          align-items: flex-start;
          justify-content: center;
          flex: 1;
          min-width: 0;
        }
        .iconType {
          width: 60px;
          height: 60px;
          margin-right: 10px;
        }
        .iconType1 {
          background: url('../../assets/img/task/choose_wechat.png') no-repeat center;
          background-size: 60px 48px;
        }
        .iconType2 {
          background: url('../../assets/img/task/choose_phone.png') no-repeat center;
          background-size: 31px 52px;
        }
        .nickname {
          text-align: left;
          overflow: hidden;
          max-width: 100%;
          font-size: 30px;
          line-height: 1.3;
          color: #666;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .vcoin {
          font-size: 24px;
          line-height: 1.3;
          color: #FF4112;
        }
        .selectIcon {
          width: 46px;
          height: 46px;
        }
      }
    }
    .buttonGroup {
      display: flex;
      margin-top: 12px;
      border-top: 1px solid #f2f2f2;
      height: 98px;
      line-height: 98px;
      font-size: 30px;
      justify-content: space-between;
      > div {
        flex: 1;
        height: 100%;
      }
      .cancel {
        color: #666;
        border-right: 1px solid #f2f2f2;
      }
      .confirm {
        color: #ddd
      }
      .confirm.active {
        color: #FF7F18;
      }
    }
  }
}
</style>



