<template>
  <div id="connectWechat" style="z-index: 12">
    <div class="connectWechat posC">
      <div class="close bgIcon" @click="close"></div>
      <div class="title flex-row"></div>
      <div class="buttonGroup flex-row" @click="confirm">
        
      </div>
      <div class="iconSize wechatIcon posC">
        <img src="../../assets/img/task/bind_wechat.png" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    close () {
      this.$emit('closeWechat')
    },
    confirm () {
      this.$emit('closeWechat', true)
    }
  }
}
</script>

<style lang="scss" scoped>
#connectWechat{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  background: rgba($color: #000000, $alpha: .56);
  font-size: 30px;
  .connectWechat {
    width: 616px;
    height: 354px;
    background: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(135,134,134,0.50);
    border-radius: 20px;
    box-sizing: border-box;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    padding: 0 40px;
    text-align: center;
    .title{
      font-size: 32px;
      margin-top: 130px;
      margin-bottom: 55px;
      line-height: 45px;
    }
    .close{
      position: absolute;
      right: 20px;
      top: 20px;
      background-image: url('../../assets/img/task/close_gray.png');
      background-size: 26px;
      height: 26px;
      width: 46px;
      height: 46px;
      padding: 20px;
      box-sizing: border-box;
    }
    .buttonGroup{
      background: #00a553;
      height: 70px;
      border-radius: 10px;
      width: 360px;
      height: 78px;
      color: #fff;
      border-radius: 39px;
      margin: 0 auto;
    }
    .wechatIcon{
      width: 156px;
      height: 125px;
      top: -60px;
    }
  }
}
</style>



