<template>
  <transition name="drop">
    <!-- <v-touch @swipedown="closeFn"> -->
    <div id="dialogBottom" style='z-index: 4' @click="closeFn">
      <slot></slot>
    </div>
    <!-- </v-touch> -->
  </transition>
</template>
<script>
export default {
  name: 'dialogBottom',
  methods: {
    closeFn () {
      this.$emit('closeReply')
    },
    swipedown () {
      
    }
  },
  mounted () {
    document.getElementById('dialogBottom').addEventListener('swipeup', () => {
      console.log(23)
    })
  }
}
</script>

<style lang="scss" type="text/css">
#dialogBottom{
  width: 750px;
  height: 100vh;
  position: fixed;
  top: 0;
  background: rgba($color: #000000, $alpha: .7);
  z-index: 10;
}
</style>



