<template>
  <div id="headerNav" :class="`flex-row jusC ${isShopDetail ? 'isShop' : ''}`">
    <div class="leftBack bgIcon" v-if="isShopDetail" @click="back"></div>
    <div class="leftDiv bgIcon flex-row" v-else @click="back"></div>
    <div class="title" v-if="!isShopDetail">{{msg ? msg : $route.meta.title}}</div>
    <div v-if='showShare' @click="headerShare" class="right">
      <div class="carIconBack" v-if="isShopDetail"></div>
      <div class="shareIconBlack" v-else></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    msg: {
      type: String,
      default: ''
    },
    isShopDetail: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      showShare: false
    }
  },
  methods: {
    back () {
      // let name = this.$route.name
      this.$router.go(-1)
      // setTimeout(() => {
      //   if (this.$route.name === name) {
      //     this.$router.replace('/')
      //   }
      // }, 400)
    },
    headerShare () {
      // let data = {
      //   btn_name: 'profit_btn_show_share'
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {})
      if (!this.$store.state.common.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      this.$emit('headerShare')
    }
  },
  created () {
    // 6
    if (this.$config.source_style >= 6) {
      let paths = ['/saveimage', '/invite_friend', '/my/profit', 'CarDetail', 'HouseDetail', 'GoodsDetail', 'browser']
      for (let v of paths) {
        if (this.$route.path === v || this.$route.name === v) {
          this.showShare = true
        }
      }
    }
  },
  mounted () {
    
  }
}
</script>
<style lang="scss" scoped>
#headerNav{
  height: 88px;
  position: fixed;
  display: flex;
  background: #fff;
  box-sizing: border-box;
  color: #222;
  left: 0;
  right: 0;
  z-index: 2;
  font-size: 34px;
  border-bottom: 1px solid #f2f2f2;
  top: 0px;
  transform: translate(0,0,0);
  &.isShop {
    background: transparent;
    background: rgba($color: #000000, $alpha: 0);
    border-bottom: 0;
  }
  .leftDiv{
    position: absolute;
    left: 0;
    width: 100px;
    height: 100%;
    top: 0;
    background-image: url('../../assets/img/black-left.png');
    background-size: 20px 35px;
  }
  .leftBack {
    position: absolute;
    width: 56px;
    height: 56px;
    top: 9px;
    background: url('../../assets/img/details/back.png') no-repeat center;
    background-size: contain;
    left: 30px;
  }
  .right{
    height: 88px;
    line-height: 88px;
    font-size: 30px;
    position: absolute;
    right: 0;
    padding-right: 30px;
    .shareIconBlack {
      height: 90px;
    }
    .carIconBack {
      width: 56px;
      height: 56px;
      background: url('../../assets/img/details/share.png') no-repeat center;
      background-size: contain;
      margin-top: 9px;
    }
  }
}
</style>



