<template>
  <div id="shareBox" class="maskBox" :class="{noQQ: showTreasureBox}" @click="shareAction('cancel')" style="z-index: 15">
    <!-- // @click.stop="'' -->
    <div class="shareContent" :class="{active: active }">
      <div class="shareWayTop">
        <div class="viewbtn" @click="jumpgetfanpageBtn"></div>
      </div>
      <div class="shareWay">
        <!--  -->
        <div class="shareFlex flex-row" @click.stop="shareAction('system')">
          <div class="iconSize">
            <img src="../../assets/img/getfan.png" alt="">
            <div class="recommend_tag"></div>
          </div>
          <div class="text"></div>
        </div>
        <!--  -->
        <div class="shareFlex flex-row" @click.stop="shareAction('sinaweibo')">
          <div class="iconSize">
            <img src="../../assets/img/weibo.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!-- QQ -->
        <div class="shareFlex flex-row" @click.stop="shareAction('qq')">
          <div class="iconSize">
            <img src="../../assets/img/qq.png" alt="QQ">
          </div>
          <div class="text">QQ</div>
        </div>
        <!--  -->
        <div class="shareFlex flex-row" @click.stop="shareAction('wechat')">
          <div class="iconSize">
            <img src="../../assets/img/wechat.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!--  -->
        <!-- <div class="shareFlex flex-row" @click.stop="shareAction('friend')">
          <div class="iconSize">
            <img src="../../assets/img/friend.png" alt="">
          </div>
          <div class="text"></div>
        </div> -->
        <!--  -->
        <div class="shareFlex flex-row copybutton" @click="getcopyLinkbtn('copy')" :data-clipboard-text="cppyLink">
          <div class="iconSize copyicon">
            <img src="../../assets/img/copybtn.png" alt="">
          </div>
          <div class="text"></div>
        </div>
        <!-- <div class="shareFlex flex-row" @click.stop="shareAction('msg')">
          <div class="iconSize">
            <img src="../../assets/img/msgshare.png" alt="">
          </div>
          <div class="text"></div>
        </div> -->
      </div>
      <div class="cancel flex-row" @click="shareAction('cancel')"></div>
    </div>
  </div>
</template>
<script>
import Clipboard from 'clipboard'
import { mapState } from 'vuex'
export default {
  props: {
    showTreasureBox: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      active: false,
      cppyLink: ''  // 
    }
  },
  computed: mapState({
    commonState: state => state.common,
    comment: state => state.comment,
    login_bol: state => state.login_bol
  }),
  methods: {
    jumpgetfanpageBtn () {
      this.$router.push({name: 'LockfanPage'})
    },
    shareAction (action) {
      if (action !== 'cancel') {
        // name = this.$route.name
        // 
        /* 
        wechat: btn_share_wexin,
        friend: btn_share_moments,
        qq: btn_share_qq,
        sina: btn_share_sina
        */
      //  let data = {}
      //   if (action === 'wechat') {
      //    let data = {
      //      btn_name:  name + 'btn_share_wexin'
      //    }
      //   } else if (action === 'friend') {
      //     let data = {
      //       btn_name: name + 'btn_share_moments'
      //     }
      //   } else if (action === 'qq') {
      //     let data = {
      //       btn_name: name + 'btn_share_qq'
      //     }
      //   } else if (action === 'sinaweibo') {
      //     let data = {
      //       btn_name: name + 'btn_share_sina'
      //     }
      //   }
      //   this.$api.post('TOTAL_CLICKBTN', data, res => {
      //   })
      }
      this.$emit('shareAction', action)
    },
    // 
    // 
    getcopyLinkbtn (type) {
      // 
      /*
        copy: btn_share_link
      */
      // let data  = {
      //   btn_name: 'copy-btn-share-link'
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {
      // })
      if (!this.commonState.login_bol) {
        if (this.$utils.isWx()) {
          this.$utils.wxLogin()
        } else {
          this.$store.commit('common_login_box', true)
        }
        return
      }
      if (this.$utils.isWx()) {
        if (this.$route.query.inviteCode) {
          let shareSuccessObj = {}
          let shareArc = this.$utils.cache('shareArc') ? JSON.parse(this.$utils.cache('shareArc')) : null
          if (shareArc && shareArc.timestamp * 1 <= this.$utils.getTimeStamp()) {
            shareArc = null
            this.$utils.cache('shareArc', null)
          }
          if (!shareArc) {
            shareSuccessObj = {
              makeRate: 0.5,
              hasGet: 1256
            }
          } else {
            shareSuccessObj = {
              makeRate: shareArc.count * 0.5 + 0.5,
              hasGet: 1256
            }
          }
          this.set_shareSuccessObj({
            type: 2,
            shareSuccessObj
          })
          // 
          this.set_shareType(2)
          this.setShowShare(true)
        } else {
          this.shareBol = true
        }
      } else {
        // inviteCode
        // qq
        if (Clipboard.isSupported()) {
          this.cppyLink = this.$config.currentWebUrl + '?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
          let clb = new Clipboard('.copybutton')
          console.log(this.cppyLink)
          clb.on('success',  (e) => {
            this.$toast({
              message: '',
              duration: 1500
            })
            clb.destroy()
          })
          clb.on('error',  (e) => {
            this.$toast({
              message: '',
              duration: 1500
            })
            clb.destroy()
          })
          this.copyflag = true
        }
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.active = true
    })
  }
}
</script>
<style lang="scss" scoped>
#shareBox {
  @keyframes moveTo {
    from {
      bottom: -500px;
    }
    to {
      bottom: 24px;
    }
  }
  .shareContent {
    position: absolute;
    width: 720px;
    bottom: 14px;
    height: 0;
    transform: translate3d(-50%, 0, 0);
    overflow: hidden;
    top: auto;
    animation: moveTo 300ms ease;
    .shareWay {
      height: 435px;
      // margin-top: 51px;
      // margin-bottom: 41px;
      margin-top: 33px;
      .shareFlex {
        display: inline-block;
        .iconSize {
          width: 110px;
          height: 110px;
          position: relative;
          margin: 0 auto;
          img {
            width: 100%;
            height: 100%;
          }
          .recommend_tag {
            position: absolute;
            top: -10px;
            right: -40px;
            width: 80px;
            height: 40px;
            background: url('../../assets/img/lockfan/recommend_tag.png') no-repeat left top;
            background-size: 100% 100%;
          }
        }
        .text {
          color: #232323;
          line-height: 40px;
          font-size: 30px;
          margin-top: 22px;
        }
        &:nth-child(1) {
          margin-left: 34px;
        }
        &:nth-child(n+1) {
          margin-left: 40px;
          margin-right: 10px;
        }
        &:nth-child(4n + 1) {
          margin-left: 42px;
        }
        &:nth-child(5) {
          margin-top: 56px;
        }
      }
    }
    .cancel {
      border-top: 1px solid #dadada;
      font-size: 32px;
      color: #666;
      height: 84px;
      line-height: 84px;
      text-align: center;
      bottom: 0;
    }
  }
  .shareWayTop {
    width: 100%;
    height: 169px;
    background: url("../../assets/img/banner.png") no-repeat center center;
    background-size: 100% 169px;
    .viewbtn {
      width: 160px;
      height: 64px;
      background:url("../../assets/img/viewbtn.gif") ;
      background-size: 160px 64px;
      margin-left: 540px;
      position: absolute;
      top: 40px;
    }
  }
  .shareContent.active {
    height: 723px;
  }
  &.noQQ {
    .shareFlex:nth-child(3){
      display: none;
    }
  }
}
</style>



