<template>
  <div id="stateless-head" class="text-center">
    <div class="inline-block Logo">
      <img :src="headData.imgUrl" alt="">
    </div>
    <div class="mT27 colorB9 f26 txt">
      {{headData.txt}}
    </div>
    <div class="clear recommend pT25 pB10">
      <div class="fl pB20 f36">
        
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'stateless-head',
  props: ['headData']
}
</script>

<style lang="scss" scoped>
#stateless-head{
  padding-top: 143px;
  .txt{
    padding-bottom: 156px;
    border-bottom: 1px solid #dbdbdb;
  }
  .recommend{
    padding-left: 32.9px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .Logo{
    width: 116px;
    height: 116px;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
</style>



