<template>
  <div id="replyList" class="v-tocuh" v-if="item" ref="replyList">
    <div class="replyTitle">
      <div class="title"></div>
      <div class="close" @click="closeFn"></div>
    </div>
    <div class="replyScroll" @click.stop="" ref="replyScroll">
      <div class="father clear li">
        <div class="left fl">
          <div class="img">
            <img :src="item.avatar" alt=""
            onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
          </div>
        </div>
        <div class="right fl">
          <div class="p1 clear">
            <div class="fl">{{item.nickname}}</div>
          </div>
          <div class="p2">
            {{item.content}}
          </div>
          <div class="p3 clear">
            <div class="fl">{{$utils.timeFormat(item.comment_time*1000)}}</div>
            <div class="fl mL30 Reply">{{$utils.thousandRead(item.reply)}} </div>
            <div class="digg firstdigg" :class="{active: item.is_fabulous === 1}">
              <div class="thumbs mR10" @click="thumbFn">
                <img src="../../assets/img/index/digg_active.png" v-if="item.is_fabulous === 1" alt="">
                <img src="../../assets/img/index/digg.png" v-else alt="">
              </div>
              <div>{{$utils.thousandRead(item.fabulous)}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="content">
        
      </div>
      <div class="list" v-if="list.length!==0">
        <div class="clear li" v-for="(itemList, index) in list" :key="itemList.rid">
          <div class="left fl">
            <div class="img">
              <img :src="itemList.avatar" alt=""
              onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
            </div>
          </div>
          <div class="right fl">
            <div class="p1 clear">
              <div class="fl">{{itemList.nickname}}</div>
            </div>
            <div class="p2">
              {{$utils.trim(itemList.content)}}
            </div>
            <div class="p3 clear">
              <div class="fl">{{$utils.timeFormat(itemList.create_time*1000)}}</div>
            </div>
          </div>
          <div class="digg" :class="{active: itemList.is_fabulous === 1}" @click="replyThumb(itemList, index)">
            <div class="thumbs mR10">
              <img src="../../assets/img/index/digg_active.png"  v-if="itemList.is_fabulous === 1" alt="">
              <img src="../../assets/img/index/digg.png" v-else alt="">
            </div>
            <div class="">{{$utils.thousandRead(itemList.fabulous)}}</div>
          </div>
        </div>
        <div class="no-more-data" v-if="isLoading === 2">{{commonState.noMessageTip}}</div>
      </div>
      <div class="isLoading" v-if="isLoadingReply === 1 || isLoading === 1">
        <mt-spinner type="snake"></mt-spinner>
        <div class="loadingText"></div>
      </div>
      <div class="noReply f24 text-center color999" v-if="list.length === 0 && isLoadingReply === 0">
        <div class="comment-icon">
          <img src="../../assets/img/details/comment-icon.png" alt="">
        </div>
        <div class="text-tip">~</div>
      </div>
    </div>
    <div class="footer clear" ref="footer" @click.stop="">
      <div class="fl input1">
        <input type="text" v-model="content" id="release" placeholder="..." @focus="inputFocus" @blur="inputBlur">
      </div>
      <div class="fl input2" @click="release">
        
      </div>
      <!--  -->
      <div class="mask" v-if="inpuBol"></div>
    </div>
  </div>
</template>

<script>
import '../../assets/css/article.scss'
import '../../assets/js/touch.js'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'replyList',
  props: {
    item: {
      type: Object
    },
    list: {
      type: Array
    },
    isLoadingReply: {
      type: Number
    },
    replyTotal: {
      type: Number,
      default: 0
    },
    details: {
      type: Object,
      default: null
    },
    isVideo: {
      type: Number
    }
  },
  computed: mapState({
    commonState: state => state.common,
    userInfo: state => state.common.userInfo
  }),
  data () {
    return {
      inpuBol: false,        // input
      content: '',           // 
      page: 1,                // 
      isLoading: 0
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    swipedown () {

    },
    swipeLeft () {

    },
    swipeRight () {
      
    },
    setTouch () {

    },
    // 
    release () {
      if (this.content.trim().length === 0) {
        this.$toast({
          message: '',
          position: 'middle',
          duration: 1500
        })
        return
      } else if (this.content.length > 100) {
        this.$toast({
          message: '100',
          position: 'middle',
          duration: 1500
        })
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = this.item.cid
      data.content = this.content.trim()
      let constant = 'REPLY_COMMENT'
      if (this.isVideo === 1) {
        data.vid = this.$route.params.aid * 1
        constant = 'VIDEO_REOLY_COMMENT'
      } else if (this.isVideo === 2) {
        data.vod_id = this.$route.params.id
        constant = 'MOVIE_REPLY'
      }
      this.$api.post(constant, data, (res) => {
        this.item.reply++
        this.page = 1        // 
        this.$emit('getList', this.item)
        this.$nextTick(() => {
          this.$refs.replyList.scrollLeft = 0
          this.$refs.replyList.scrollTop = 0
        })
        this.content = ''
      })
    },
    // 
    replyThumb (item, index) {
      // 
      if (this.$route.query.inviteCode && this.$utils.isQQ()) {
        return
      }
      if (item.is_fabulous === 1) {
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = this.item.cid
      data.rid = item.rid
      let url = 'FABULOUS'
      if (this.isVideo === 1) {
        url = 'VIDEO_DIGG'
        data.vid = data.aid
        delete data.aid
      } else if (this.isVideo === 2) {
        url = 'MOVIE_FABULOUS'
        data.vod_id = this.$route.params.id
        delete data.aid
      }
      this.$api.post(url, data, (res) => {
        this.list[index].is_fabulous = 1
        this.list[index].fabulous++
      })
    },
    // 
    thumbFn () {
      // 
      if (this.$route.query.inviteCode && this.$utils.isQQ()) {
        return
      }
      if (this.item.is_fabulous === 1) {
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = this.item.cid
      let url = 'FABULOUS'
      if (this.isVideo === 1) {
        data.vid = data.aid
        delete data.aid
        url = 'VIDEO_DIGG'
      } else if (this.isVideo === 2) {
        data.vod_id = this.$route.params.id
        url = 'MOVIE_FABULOUS'
      }
      this.$api.post(url, data, (res) => {
        this.item.is_fabulous = 1
        this.item.fabulous++
      })
    },
    // input
    inputBlur () {
      this.inpuBol = false
    },
    // input
    inputFocus () {
      this.inpuBol = true
      setTimeout(() => {
        this.$refs.footer.scrollTop = '-100px'
      }, 300)
    },
    // 
    closeFn () {
      this.$emit('closeReply')
    },
    // 
    upList () {
      if (this.isLoading === 2) {
        return
      }
      this.ul = this.$refs.replyScroll.getElementsByClassName('list')
      if (this.ul[0]) {
        if (this.ul[0].clientHeight - this.$refs.replyScroll.scrollTop && this.isLoading === 0) {
          this.isLoading = 1
          if (this.replyTotal > this.list.length) {
            if (this.timer) {
              clearInterval(this.timer)
              this.timer = null
            }
            this.timer = setTimeout(() => {
              this.$api.get('REPLY_LIST', {cid: this.item.cid, page: this.page}, (res) => {
                console.log(res)
                let lists = res.data.data
                this.isLoading = 0
                if (this.page >= res.data.last_page) {
                  this.isLoading = 2
                }
                this.$emit('addlist', lists)    // 
                this.page++
              }, 200)
            })
          }
        }
      }
    },
    // 
    scrHeight () {
      const oDiv = this.$refs.replyList
      let scrollTop = oDiv.scrollTop
      let clientHeight = 0
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
      } else {
        clientHeight = (document.body.clientHeight > document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
      }
      return oDiv.scrollHeight - scrollTop - clientHeight
    }
  },
  mounted () {
    this.$nextTick(() => {
      if (this.$refs.replyList) {
        this.page = 1
        console.log(this.$refs.replyList)
        this.ul = this.$refs.replyScroll.getElementsByClassName('list')[0]
        this.$refs.replyScroll.addEventListener('scroll', this.upList)
      }
    })
  },
  destroyed () {

    // this.$store.commit('common_set_scroll', true)
  }
}

</script>



