<template>
  <!--  -->
  <div id="confirmBox" class="maskBox" style='z-index: 10'>
    <div class="confirmBox" v-if="config.invite_self > 0">
      <div class="special iconSize posC">
        <img src="../../assets/img/task/special_tip.png" alt="">
      </div>
      <div class="title">{{$utils.splitStr(config.wechat_nickname)}}?</div>
      <div class="content"></div>
      <div class="buttonGroup flex-row">
        <div class="button cancel" @click="close('cancel')">{{params.cancelText ? params.cancelText: ''}}</div>
        <div class="button confirm" @click="close('confirm')">{{params.confirmText ? params.confirmText: ''}}</div>
      </div>
    </div>
    <div class="confirmBox" v-else>
      <div class="special iconSize posC">
        <img src="../../assets/img/task/special_tip.png" alt="">
      </div>
      <div class="title">{{$utils.splitStr(config.wechat_nickname)}}?</div>
      <div class="content"><span class="redColor"></span></div>
      <div class="buttonGroup flex-row">
        <div class="button cancel" @click="close('cancel')">{{params.cancelText ? params.cancelText: ''}}</div>
        <div class="button confirm" @click="close('confirm')">{{params.confirmText ? params.confirmText: ''}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    config: {
      type: Object
    }
  },
  data () {
    return {
      params: {}
    }
  },
  methods: {
    close (action) {
      this.$emit('closeConfirmBox', {action})
    }
  },
  mounted () {
    this.params = this.config ? this.config : {}
  }
}
</script>

<style lang="scss" scoped>
#confirmBox{
  .confirmBox{
    width: 610px;
    font-size: 30px;
    .title{
      padding: 104px 60px 20px;
      line-height: 40px;
      font-size: 28px;
      color: #666;
      font-weight: 900;
    }
    .special {
      top: -162px;
      width: 417px;
      height: 229px;
    }
    .content{
      font-size: 30px;
      padding: 20px 60px 50px;
      line-height: 40px;
      color: #333;
      font-weight: 900;
    }
    .buttonGroup{
      height: 88px;
      border-top: 1px solid #f1f1f1;
      .button {
        width: 50%;
        flex: 1;
        height: 100%;
        text-align: center;
        line-height: 88px;
      }
      .cancel {
        color: #666;
        font-weight: 900;
      }
      .confirm {
        border-left: 1px solid #f2f2f2;
        color: #f6a00a;
        font-weight: 900;
      }
    }
  }
}
</style>



