<template>
<div class="position_view" :id="`adClick${position}`" :class="`position${view.position}`" @click="setAdClicks()">
  <div class="adLink" :class="{isAndroid: $utils.isAndroid()}">
    <div class="m-rect" v-if="view.show === 1 || (view.show === 0 && view.title)"></div>
    <!-- 0 1 2  3 -->
    <div v-if="view.show === 2" class="loopImage">
      <swiper class="swiper" :options="swiperOptions" ref="swiper">
        <swiper-slide class="swiperSlide" v-for="(item, index) in view.img" :key="index">
          <div class="iconSize">
            <img :src="item" alt="">
          </div>
        </swiper-slide>
      </swiper>
      <div class="muti-bottom">
        <div class="ad-tag" :class="{isAndroid: $utils.isAndroid()}"></div>
        <div class="virtual_read_num">{{view.virtual_read_num | setPeople}}</div>
      </div>
      <div class="m-rect"></div>
    </div>
    <!--  -->
    <div v-if="view.show === 0 && !view.title" class="oneImage image">
      <div class="iconSize">
        <img :src="typeof view.img === 'string' ? view.img: view.img[0]" alt="">
      </div>
      <div class="muti-bottom">
        <div class="ad-tag" :class="{isAndroid: $utils.isAndroid()}"></div>
        <div class="virtual_read_num">{{view.virtual_read_num | setPeople}}</div>
      </div>
    </div>
    <div class="deleteIcon" @click.stop="closeFn" v-if="view.showDelete"></div>
    <!--  -->
    <div v-if="view.show === 0 && view.title" class="oneImage hasTitle">
      <div class="title">{{view.title}}</div>
      <div class="iconSize">
        <img :src="typeof view.img === 'string' ? view.img: view.img[0]" alt="">
      </div>
      <div class="muti-bottom">
        <div class="ad-tag" :class="{isAndroid: $utils.isAndroid()}"></div>
        <div class="virtual_read_num">{{view.virtual_read_num | setPeople}}</div>
      </div>
    </div>
    <!--  -->
    <div class="muti" v-if="view.show === 1">
      <div class="title" v-if="view.title">{{view.title}}</div>
      <div class="imgBox">
        <div class="iconSize" v-for="(item, index) in view.img" :key="index">
          <img :src="item" alt="">
        </div>
      </div>
      <div class="muti-bottom">
        <div class="ad-tag" :class="{isAndroid: $utils.isAndroid()}"></div>
        <div class="virtual_read_num">{{view.virtual_read_num | setPeople}}</div>
      </div>
    </div>
    <div class="m-rect" v-if="view.show === 1 || (view.show === 0 && view.title)"></div>
  </div>
  <div class="deleteIcon" @click.stop="closeFn" v-if="view.showDelete"></div>
</div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOptions: {
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        loop: true
      },
      isClick: false
    }
  },
  props: {
    view: {
      type: Object
    },
    position: {
      type: Number
    }
  },
  methods: {
    closeFn () {
      this.$emit('closeFn', this.view.id)
    },
    setAdClicks () {
      // 
      if (this.isClick) {
        this.$utils.openAd(this.view.url)
      } else {
        this.isClick = true
        let data = {
          id: this.view.id,
          from: this.$utils.isWx() ? 'weixin' : 'wap'
        }
        this.$api.get('ADCLICK', data, res => {
          this.isClick = false
        })
        this.$utils.openAd(this.view.url)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.position_view {
  min-height: 150px;
  width: 100%;
  background: #fff;
  > div {
    height: 100%;
  }
  .adLink.isAndroid {
    .ad-tag {
      height: 26px;
      line-height: 32px;
    }
  }
  // 3
  &.position3 {
    // background: #f2f2f2;
    width: 690px;
    margin: 30px auto 0;
    overflow: hidden;
    border-radius: 20px;
  }
  &.position4 {
    box-shadow: 0 -2px 8px rgba($color: #000000, $alpha: .08);
  }
  .deleteIcon {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 20px;
    top: 20px;
    background: url('../../assets/img/details/delete_icon.png') no-repeat center;
    background-size: 100%;
  }
  .muti-bottom {
    display: flex;
    align-items: center;
    padding: 10px 0 15px;
    .ad-tag {
      font-size: 20px;
      line-height: 26px;
      margin-right: 10px;
      // color: #ED575E;
      background: #ccc;
      color: #fff;
      box-sizing: border-box;
      border-radius: 6px;
      padding: 0 4px;
    }
    .virtual_read_num {
      margin-right: 20px;
      font-size: 24px;
      line-height: 26px;
      max-width: 180px;
      color: #999;
    }
  }
  // 
  .oneImage {
    padding: 10px;
    height: 100%;
    box-sizing: border-box;
    &.hasTitle {
      padding: 0;
      .iconSize {
        border-radius: 10px;
        overflow: hidden;
      }
    }
    .title {
      font-size: 36px;
      padding-top: 30px;
      line-height: 50px;
      max-height: 150px;
      overflow: hidden;
      padding-bottom: 20px;
    }
    .iconSize {
      width: 100%;
      height: 100%;
    }
  }
  // 
  .loopImage {
    height: 100%;
    .swiper {
      width: 705px;
      margin: 0 auto;
      height: 100%;
      .swiperSlide {
        height: 100%;
        box-sizing: border-box;
        margin-bottom: 10px;
        .iconSize {
          width: 100%;
          height: 100%;
        }
      }
    }
    .m-rect {
      margin-top: 15px;
    }
    .muti-bottom {
      padding: 0 30px;
    }
  }
  .m-rect {
    width: 750px;
    height: 10px;
    margin-left: -30px;
    background: #eee;
  }
  // 
  .muti {
    padding: 20px 0 15px;
    .title {
      font-size: 36px;
      line-height: 50px;
      max-height: 150px;
      overflow: hidden;
      padding-bottom: 20px;
    }
    .imgBox {
      display: flex;
      justify-content: flex-start;
      .iconSize {
        width: 226px;
        height: 150px;
        border-radius: 10px;
        overflow: hidden;
      }
      .iconSize:nth-child(3n + 2) {
        margin: 0 10px;
      }
    }
    .muti-bottom {
      display: flex;
      align-items: center;
      padding: 10px 0 0px;
      .ad-tag {
        font-size: 20px;
        line-height: 26px;
        margin-right: 10px;
        color: #fff;
        box-sizing: border-box;
      }
      .virtual_read_num {
        margin-right: 20px;
        font-size: 24px;
        line-height: 26px;
        max-width: 180px;
        color: #999;
      }
    }
  }
}
</style>



