<template>
  <div class="mutiAdFloat">
    <div class="img-wrapper" @click="setClick">
      <img :src="adObj.images[0]" alt="">
      <div class="ad_tag"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    adObj: {
      type: Object,
      default () {
        return {
          isClick: false
        }
      }
    }
  },
  methods: {
    setClick () {
      this.$utils.openAd(this.adObj.link)
      if (!this.isClick) {
        this.$api.get('AdvTOTAL', {
          ad_id: this.adObj.id,
          type: 1,
          uuid: this.$store.state.common.uuid
        })
      }
    },
    setBrowserNums () {
      this.$api.get('AdvTOTAL', {
        ad_id: this.adObj.id,
        type: 0,
        uuid: this.$store.state.common.uuid
      })
    }
  },
  mounted () {
    this.setBrowserNums()
  },
  watch: {
    '$route.params.id' (id) {
      if (id) {
        this.setAd()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.mutiAdFloat {
  width: 182px;
  // height: 182px;
  border-radius: 4px;
  overflow: hidden;
  .img-wrapper {
    text-align: center;
    vertical-align: top;
    height: 100%;
    .ad_tag {
      font-size: 16px;
      line-height: 32px;
      width: 50px;
      border-radius: 6px;
      background: rgba(0, 0, 0, .5);
      color: #fff;
      box-sizing: border-box;
      position: absolute;
      right: 12px;
      top: 12px;
    }
    img {
      width: 100%;
      display: inline-block;
    }
  }
}
</style>



