<script>
export default {
  data () {
    return {
      tipIndex: '',
      showTip: false,
      confirmText: ''
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    },
    share () {
      return this.$store.state.share
    }
  },
  props: {
    list: {                 // 
      type: Array
    },
    type: {                 // 
      type: Number,
      default: 0
    },
    activeType: {           // 
      type: Object
    },
    filterType: {           // 
      type: String
    },
    movieType: {
      type: Number          // 1  2  3  4
    },
    activeIndex: {          //  
      type: Number
    }
  },
  methods: {
    setIndex (index) {
      this.setHistory(this.list[index])
      this.$router.push('/film/detail/' + index)
    },
    // 
    setTip (index) {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      this.$emit('setTip', {index})
    },
    // 
    toDetail (index) {
      if (this.type === 8) {
        if (this.list[index].type === 'douban') {
          // this.$toast({
          //   message: `${this.list[index].info.title}`,
          //   duration: 1500
          // })
          this.$store.commit('set_shareSuccessObj', {
            type: 18,
            shareSuccessObj: {
              msg: `${this.list[index].info.title}`
            }
          })
          this.$store.commit('setShowShare', true)
          this.$store.commit('set_shareType', 18)
        } else {
          this.setHistory(this.list[index].info)
          this.$router.push('/film/detail/' + this.list[index].vod_id)
        }
      } else {
        this.setHistory(this.list[index])
        this.$router.push('/film/detail/' + this.list[index].vod_id)
      }
    },
    // 
    toSearch (index) {
      let movieType = ''
      if (this.activeIndex) {
        movieType = this.activeIndex
      } else if (this.movieType) {
        movieType = this.movieType
      }
      this.$emit('toSearch', {
        item: this.list[index],
        movieType
      })
      // this.$router.push('/movie/search?kw=' + this.list[index].title)
    },
    serFilter (index) {
      let movieType = ''
      if (this.activeIndex) {
        movieType = this.activeIndex
      } else if (this.movieType) {
        movieType = this.movieType
      }
      this.list[index].title = this.list[index].vod_name
      this.$emit('toSearch', {
        item: this.list[index],
        movieType
      })
    },
    // 
    setHistory (item) {
      let arr = this.$utils.cache('historys') ? JSON.parse(this.$utils.cache('historys')) : []
      if (!item.vod_id) {
        return
      }
      for (let i of arr.keys()) {
        if (arr[i].type === 'movie' && arr[i].item.vod_id === item.vod_id) {
          arr.splice(i, 1)
          break
        }
      }
      let obj = {
        item,
        create_time: this.$utils.getTimeStamp(),
        type: 'movie'
      }
      // item.collect_time = this.$utils.getTimeStamp()
      // item.type = 'movie'
      arr.unshift(obj)
      this.$utils.cache('historys', JSON.stringify(arr))
    }
  }
}
</script>
