          height: 265px;
        }
        .right {
          position: relative;
          flex: 1;
          .title {
            padding: 2px 0 0 0;
            line-height: 45px;
            font-size: 32px;
            font-weight: 900;
            margin-bottom: 14px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          .tip {
            line-height: 36px;
          }
          .button {
            width: 112px;
            height: 43px;
            text-align: center;
            line-height: 45px;
            font-size: 24px;
            position: absolute;
            bottom: 0px;
            border: 1px solid #F4A82B;
            color: #F4A82B;
            border-radius: 3px;
            left: 0;
            &.active {
              color: #666;
              border-color: #666;
            }
            .tip_text {
              &.active {
                display: flex;
                align-items: center;
                justify-content: center;
              }
              .image {
                width: 26px;
                height: 18px;
                display: flex;
              }
            }
          }
          .search {
            position: absolute;
            bottom: 0px;
            right: 0px;
            font-size: 28px;
            align-items: center;
            color: #6BA5D8;
            line-height: 1px;
            .text {
              line-height: 45px;
              font-size: 28px;
            }
            .arrowEl {
              margin-left: 0px;
              border-color: rgba($color: #6BA5D8, $alpha: .76);
              transform-origin: right left;
              width: 15px;
              height: 15px;
              margin-bottom: 2px;
            }
          }
        }
      }
    }
    &.type7 {
      .list-item {
        width: 100%;
        flex-flow: row nowrap;
        display: flex;
        margin: 0px 15px 20px;
        padding: 30px 0 20px;
        box-shadow: 0 1px 0 0 #EBEAEF;
        border: 0;
        .item-cover{
          width: 189px;
          height: 264px;
          position: relative;
          overflow: hidden;
          .publish-time {
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
            z-index: 1;
            bottom: 0;
            line-height: 45px;
            font-size: 24px;
            white-space: nowrap;
            color: #fff;
          }
        }
        .name {
          min-width: 0;
          flex: 1;
          > div {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          .grade {
            margin-bottom: 20px;
          }
          .list-item-title {
            font-size: 32px;
            font-weight: 900;
            line-height: 45px;
            padding: 20px 0 10px;
          }
          .list-item-content {
            margin-bottom: 5px;
            line-height: 40px;
            font-size: 28px;
            color: #666
          }
        }
      }
      .list-item-content {
        text-overflow: ellipsis;
      }
    }
    &.type8 {
      padding: 0;
      .list-item8 {
        width: 100%;
        padding: 0 10px;
        box-sizing: border-box;
      }
      .list-item {
        box-sizing: border-box;
        .typeList {
          // align-self: center;
          padding-top: 20px;
          font-size: 28px;
          color: #FF4112;
        }
      }
    }
    // 
    &.type9 {
      .item-cover {
        border: 1px solid rgba($color: #000000, $alpha: .5);
      }
    }
  }
  .noData {
    margin-top: 38px;
  }
  img[lazy=loading] {
    // display: none;
  }
}
</style>



