<template>
  <div id="comment-list" ref="commonlistAdbox"  class="border-box pL20 pR20" :class="{hasInviteCode: $route.query.inviteCode, isAndroid: $utils.isAndroid(), hasAd: articleAd4 &&  articleAd4.isShow}">
    <div class="text-center nuList" v-if="list.length===0">
      
    </div>
    <div class="list" v-if="list.length!==0">
      <div v-for="(item, index) in list" :key="item.index">
        <div class="clear li" v-if="item.type !== 100">
          <div class="left fl">
            <div class="img">
              <img :src="item.avatar" alt=""
              onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
            </div>
          </div>
          <div class="right fl">
            <div class="p1 clear">
              <div class="fl">{{item.nickname}}</div>
            </div>
            <div class="p2">
              <div :class="item.toggleShow?'':'text'">{{$utils.trim(item.content)}}</div>
              <span class="toggle" v-if="$utils.trim(item.content).replace(/[^\x00-\xff]/g, '01').length>124" @click="setToggleShow(index)">{{item.toggleShow?'':''}}</span>
              <!--{{$utils.trim(item.content)}}-->
            </div>
            <div class="p3 clear">
              <div class="time">{{$utils.timeFormat(item.comment_time * 1000)}}</div>
              <div v-if="replyBol" class="fl Reply" @click="replyFn(item)">
                &nbsp;<span class="replyButton" v-if="$utils.thousandRead(item.reply) > 0">&nbsp;{{$utils.thousandRead(item.reply)}}</span> >
              </div>
            </div>
            <div class="digg" @click="thumbFn(item, index)">
              <div class="digg_image iconSize">
                <img src="../../assets/img/index/digg_active.png" v-if="item.is_fabulous===1" alt="">
                <img src="../../assets/img/index/digg.png" v-else alt="">
              </div>
              <div class="digg_count" :class="{active: item.is_fabulous}">{{$utils.thousandRead(item.fabulous) === 0?'':$utils.thousandRead(item.fabulous)}}</div>
            </div>
          </div>
        </div>
        <div class="advertise" v-else>
          <div class="rect" v-if="index !== 0"></div>
          <ad-view :adObj="item" @closeAd="deleteAd(index)"></ad-view>
          <div class="rect"></div>
        </div>
      </div>
      <div class='no-more-data' v-if="commonState.isLoading > 0">{{commonState.isLoading === 1 ? '...' : $store.state.common.noMessageTip}}</div>
    </div>
  </div>
</template>
<script>
import adView from '@/components/mutiAdList'
export default {
  name: 'comment-list',
  components: {
    adView
  },
  props: {
    list: {       // 
      type: Array,
      default: null
    },
    replyBol: {       // 
      type: Boolean,
      default: true
    },
    details: { // 
      type: Object
    },
    total: {
      type: Number,
      default: 0
    },
    isVideo: {
      type: Number
    },
    articleAd4: {
      type: Object
    }
  },
  data () {
    return {
      toggleShow: false
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  methods: {
    deleteAd (index) {
      this.$emit('deleteAd', index)
    },
    setToggleShow (index) {
      if (this.list[index].toggleShow) {
        this.$set(this.list[index], 'toggleShow', false)
      } else {
        this.$set(this.list[index], 'toggleShow', true)
      }
    },
    // 
    thumbFn (item, index) {
      // 
      if (this.$route.query.inviteCode && this.$utils.isQQ()) {
        return
      }
      if (item.is_fabulous === 1) {
        return
      }
      let data = {}
      data.aid = this.$route.params.aid * 1
      data.cid = item.cid
      this.$indicator.open({
        text: '...',
        spinnerType: 'fading-circle'
      })
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      setTimeout(() => {
        this.$indicator.close()
        this.isLoading = false
      }, 5000)
      let url = 'FABULOUS'
      if (this.isVideo === 1) {
        url = 'VIDEO_DIGG'
        data.vid = data.aid
        delete data.aid
      } else if (this.isVideo === 2) {
        url = 'MOVIE_FABULOUS'
        data.vod_id = this.$route.params.id
        delete data.aid
      }
      this.$api.post(url, data, (res) => {
        this.isLoading = false
        this.$indicator.close()
        this.list[index].is_fabulous = 1
        this.list[index].fabulous++
      }, res => {
        this.isLoading = false
        this.$indicator.close()
      })
    },
    // 
    replyFn (item) {
      if (typeof item.reply_list === 'string') {
        item.reply_list = JSON.parse(item.reply_list)
      }
      this.$emit('replyFn', item)
    },
    /**
     * @method 
     * @param {string} id id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {
      })
    },
    // 
    scrollFn () {
      // 
      if (this.$refs.commonlistAdbox) {
        let ads = this.$refs.commonlistAdbox.querySelectorAll('.mutiAdList')
        if (ads && ads.length > 0) {
          console.log(ads)
          for (let v of ads) {
            if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
              this.setBrowser[v.dataset.id] = true
              console.log(this.setBrowser)
              this.setBrowserNums(v.dataset.id)
            }
          }
        }
      }
    }
  },
  filters: {
    // 
    formatDate: (millisecond) => {
      let times = (new Date().getTime()) / 1000 - millisecond
      let date = new Date(parseInt(millisecond * 1000))
      if (times <= 60) {
        return ''
      } else if (times > 60 && times <= 3600) {
        return parseInt(times / 60) + ''
      } else if (times > 3600 && times <= 86400) {
        return parseInt(times / 3600) + ''
      } else if (times > 86400 && times <= 172800) {
        return '' + date.getHours() + ':' + date.getMinutes()
      } else if (times > 172800) {
        return date.getFullYear() + '' + (date.getMonth() + 1) + '' + date.getDate() + '' + date.getHours() + ':' + date.getMinutes()
      }
    }
  }
}
</script>

<style lang="scss" scoped type="text/css">
.no-more-data {
  padding-top: 20px;
}
.advertise {
  margin-top: 20px;
  margin-bottom: -20px;
  width: 750px;
  box-sizing: border-box;
  margin-left: -30px;
  padding: 0 30px;
  .rect {
    height: 10px;
    margin-left: -30px;
    width: 750px;
    background: #f5f5f5;
  }
}
#comment-list{
  width: 100%;
  padding: 0 32px 130px;
  overflow-x: hidden;
  &.hasInviteCode {
    padding-bottom: 230px;
  }
  &.hasAd {
    padding-bottom: 300px;
  }
  &.isAndroid {
    .p3 {
      .Reply {
        transform: translateY(-0px)
      }
      .time {
        margin-top: 4px;
        font-size: 28px;
      }
    }
  }
  img{
    width: 100%;
    height: 100%;
  }
  .li{
    padding-top: 8px;
    margin-top: 55px;
    position: relative;
  }
  .right{
    padding-left: 10px;
    width: 615px;
    color: #a2a2a2;
    .thumbs{
      width: 24px;
      height: 24px;
    }
    .p1{
      font-size: 24px;
      color: rgba(102,120,140,0.86);
      line-height: 34px;
    }
    .p2{
      word-wrap: break-word;
      font-size: 32px;
      color: #333333;
      height: auto;
      padding: 0;
      margin: 0;
        .text{
          max-height: 132px;
          min-width: 0;
          width: 100%;
          display: inline-block;
          font-size: 32px;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .toggle{
          background: #f4f5f7;
          color: #a2a2a2;
          display: inline-block;
          line-height: 45px;
          font-size: 24px;
          padding: 5px 20px;
          display: inline-block;
        }
        &>div{
          line-height: 45px;
        }
    }
    .p3{
      font-size: 28px;
      line-height: 34px;
      color: #B3C5D8;
      height: 34px;
      display: flex;
      align-items: center;
    }
    .digg {
      position: absolute;
      right: 0px;
      top: 0px;
      display: flex;
      align-items: center;
      font-size: 24px;
      color: #B6c8DA;
      .digg_image{
        width: 30px;
        height: 30px;
        margin-right: 10px;
      }
      .digg_count.active{
        color: rgb(255, 96, 97)
      }

    }
    .Reply{
      text-align: center;
      font-size: 24px;
      color: #7389A2;
    }
  }
  .left{
    width: 60px;
    .img{
      width: 50px;
      height: 50px;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .nuList{
    color: #a2a2a2;
    line-height: 150px;
    font-size: 24px;
  }
}
</style>



