<script>
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    adObj: {
      type: Object,
      default () {
        return {
          adType: 1,
          id: 12,
          title: '-23494',
          sub_title: '',
          style_type: 32,
          download: 1,
          ad_name: '',
          username: '',
          link: 'http://m.juejinchain.com/#/ArticleDetails/3819618',
          images: ['http://jjb-ad.oss-cn-hongkong.aliyuncs.com/ad-files/2019/04/17/720x400-000.png', 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/test-video.mp4']
          // images: ['http://jjb-ad.oss-cn-hongkong.aliyuncs.com/ad-files/2019/04/17/720x400-000.png', 'http://jjb-ad.oss-cn-hongkong.aliyuncs.com/ad-files/2019/04/17/720x400-000.png', 'http://jjb-ad.oss-cn-hongkong.aliyuncs.com/ad-files/2019/04/17/720x400-000.png']
        }
      }
    }
  },
  data () {
    return {
      showImage: true,
      progress: 0,
      isOpenPop: false,
      // 
      adClose: 1,
      isClick: false,
      downloadStatus: 0,
      downloadTask: null,
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination'
        },
        autoplay: {
          delay: 3000
        },
        loop: true
      },
      // 
      swiperOptions1: {
        autoplay: {
          delay: 3000
        },
        slidesPerView: 3,
        spaceBetween: 10,
        pagination: {
         
        },
        loop: true
      },
      adList: [],
      showPopver: false,
      positionTop: 0
    }
  },
  methods: {
    setVideoPlay (e) {
      if (this.videoEl.paused) {
        this.videoEl.play()
        this.$nextTick(() => {
          this.showImage = false
        })
      } else {
        this.videoEl.pause()
        this.$nextTick(() => {
          this.showImage = true
        })
        // this.showImage = true
      }
    },
    setDownload (e) {
      if (this.$config.source_style === 7) {
        if (!this.adObj.download_android) {
          return this.$toast('')
        }
        this.$utils.plusReady(plus => {
          // 9.0
          var flag = window.mui.os.version[0] >= 9
          flag = false
          if (flag) {
            plus.runtime.openURL(this.adObj.download_android)
          } else {
            if (this.downloadStatus === 0) {
              this.progress = 0
              this.downloadStatus = 1
              this.filename = '_downloads/' + new Date().getTime() + '.apk'
              plus.downloader.clear()
              this.downloadTask = plus.downloader.createDownload(this.adObj.download_android, {filename: this.filename}, (d, status) => {
                if (status === 200) {
                  this.downloadStatus = 2
                  plus.io.resolveLocalFileSystemURL(this.filename, entry => {
                    plus.runtime.install(this.filename, {
                      force: true
                    }, () => {
                      // this.$toast('')
                    }, (err) => {
                      this.$toast('' + JSON.stringify(err))
                    })
                  })
                }
              })
              this.downloadTask.addEventListener('statechanged', res => {
                this.progress = Math.round(res.downloadedSize / res.totalSize * 100) ? Math.round(res.downloadedSize / res.totalSize * 100) : 0
                this.progress = Math.round(res.downloadedSize / res.totalSize * 100) ? Math.round(res.downloadedSize / res.totalSize * 100) : 0
              }, false)
              this.downloadTask.start()
            } else if (this.downloadStatus === 1) {

            } else if (this.downloadStatus === 2) {
              plus.io.resolveLocalFileSystemURL(this.filename, entry => {
                plus.runtime.install(this.filename, {
                  force: true
                }, () => {
                  // this.$toast('')
                }, (err) => {
                  this.$toast('' + JSON.stringify(err))
                })
              })
            }
          }
        })
      } else if (this.$config.source_style === 6) {
        this.$utils.plusReady(plus => {
          plus.runtime.openURL(this.adObj.download_ios)
        })
      } else {
        if (this.$utils.isIos()) {
          window.open(this.adObj.download_ios)
        } else {
          window.open(this.adObj.download_android)
        }
      }
    },
    // 
    setClick () {
      this.$utils.openAd(this.adObj.link)
      if (!this.isClick) {
        this.isClick = true
        this.$api.get('AdvTOTAL', {ad_id: this.adObj.id, type: 1, uuid: this.$store.state.common.uuid}, res => {})
      }
    },
    // 
    playVideo () {
      this.videoEl.play()
      this.showImage = false
    },
    // 
    setCloseFn (e) {
      this.isOpenPop = true
      this.$store.commit('common_set_showPopver', {
        showPopver: true,
        clientY: e.clientY
      })
    }
  },
  watch: {
    '$store.state.common.isClose' () {
      if (this.isOpenPop) {
        this.$emit('closeAd')
      }
    }
  },
  created () {

  },
  mounted () {
    if (this.videoEl) {
      this.videoEl.addEventListener('ended', () => {
        this.showImage = true
      })
    }
  },
  computed: {
    videoEl () {
      return this.$refs.mutiAdList.querySelector('video')
    }
  }
}
</script>
