    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 2;
  }
  .img-wrapper {
    width: 100%;
    position: relative;
    img {
      width: 100%;
      vertical-align: top;
    }
  }
  // 
  .adBottom {
    .img-wrapper {
      margin-top: 20px;
    }
    .title {
      padding-bottom: 0;
    }
  }
  .adFull {
    padding-top: 20px;
  }
  // 
  .threeImage {
    overflow: hidden;
    .img-wrapper {
      width: 226px;
    }
    .imgs {
      display: flex;
      .img-wrapper:nth-child(2) {
        margin: 0 10px;
      }
      margin: 20px 0 17px;
    }
    .bottom_items {
      padding-top: 14px;
    }
  }
  .threeImage-top {
    .title {
      padding: 0;
    }
  }
  .threeImage-bottom {
    .imgs {
      margin: 0;
    }
  }
  // 
  .standard {
    padding: 20px 0 0;
    .stand-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      align-items: flex-start;
      .title {
        -webkit-line-clamp: 3;
        padding: 0;
        flex: 1;
        margin-right: 20px;
      }
      .img-wrapper {
        width: 203px;
        margin-top: 10px;
      }
    }
    .bottom_items {
      padding-top: 12px;
    }
    &.standard-right {
      .title {
        margin-left: 20px;
        margin-right: 0;
      }
    }
    &.standard-full {
      padding-bottom: 20px;
      .img-wrapper {
        width: 100%;
        .title {
          padding-bottom: 0;
        }
        .bottom_items {
          position: absolute;
          left: 0;
          width: 100%;
          bottom: 12px;
          padding: 0 20px;
          box-sizing: border-box;
          .ad_tag {
            color: #fff;
            background: rgba($color: #000000, $alpha: .15);
            border: 0;
          }
          .close {
            background-image: url('../../assets/img/adview/close_sec.png');
          }
        }
      }
    }
  }
  .loop-item {
    &.loop-full {
      padding-top: 20px;
      padding-bottom: 10px;
      width: 750px;
      margin-left: -30px;
      .img-wrapper {
        position: relative;
      }
      .ad_tag {
        position: absolute;
        left: 20px;
        bottom: 15px;
        border-radius: 5px;
        background: rgba($color: #000000, $alpha: .15);
        font-size: 16px;
        color: #fff;
        width: 42px;
        height: 22px;
        text-align: center;
        line-height: 22px;
      }
    }
    &.loop-percent {
      width: 750px;
      margin-left: -30px;
      padding-top: 20px;
      .bottom_items {
        padding: 12px 20px 4px;
      }
    }
  }
  .video-type {
    .video-wrapper {
      position: relative;
    }
    video {
      display: block;
      width: 100%;
    }
    .video-popover {
      position: absolute;
      width: 100%;
      height: 100%;
      overflow: hidden;
      left: 0;
      top: 0;
      .img-popover {
        width: 100%;
        vertical-align: top;
        position: absolute;
        left: 0;
        top: 0;

      }
      .play-icon {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 80px;
        height: 80px;
        background: url('../../assets/img/adview/play.png') no-repeat center;
        background-size: 100% 100%;
        z-index: 3;
      }
    }
  }
  .video-full {
    padding-top: 20px;
  }
  .video-bottom {
    padding-top: 20px;
    .bottom_items {
      padding-top: 0;
    }
  }
  .noImage {
    .effect-right {
      padding-right: 0px!important;
    }
  }
  // 
  .effectAd {
    width: 100%;
    height: 100px;
    white-space: nowrap;
    box-sizing: border-box;
    border-top: 1px solid #f2f2f2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .description {
      color: #E92B2B;
    }
    .effect-left {
      max-width: 60%;
      display: flex;
      white-space: nowrap;
      align-items: center;
    }
    .effect-right {
      display: flex;
      flex:1;
      white-space: nowrap;
      align-items: center;
      justify-content: flex-end;
      flex-flow: row nowrap;
    }
    .imglogo {
      width: 46px;
      height: 46px;
      min-width: 46px;
      margin-right: 10px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .effectAdtitle {
      font-size: 32px;
      color: #333;
      min-width: 0;
      text-overflow: ellipsis;
      line-height: 1.1;
      overflow: hidden;
    }
    .ad_tag {
      font-size: 16px;
      height: 22px;
      line-height: 22px;
      margin-left: 6px;
      border-radius: 5px;
      padding: 0 5px;
      border: 1px solid #ccc;
      color: #ccc;
    }
    .effect-right {
      padding-right: 6px;
      .otherimg {
        width: 97px;
        height: 66px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .arrow {
      display: inline-block;
      transition: all 0.3s;
    }
    .bottomArrow{
      width: 21px;
      height: 21px;
      background-position: center;
      background-size: 21px 12px;
      background-image: url('../../assets/img/task/arrow_bottom.png');
      transition: all .3s;
      transform: rotate(-90deg);
     }
    }
  }
  .func {
    overflow: hidden;
  }
}
</style>



