<template>
  <div id="share" @click="closeShare">
    <div class="wechat-cotent">
      <div @click.stop="">
          <h1 class="step1">
          ...
          <img src="../../../assets/img/dot.png" class="dot-img" alt="">
        </h1>
        <div class="jiantou">
          <img class="wp100" src="../../../assets/img/jiantou.png" alt="">
        </div>
        <div class="imgIcon">
          <h1 class="step2"></h1>
          <div class="wechat-icon">
            <div class="wechat-icon-img">
            <img src="../../../assets/img/friend.png" class="wp100">
            </div>
            <div class="wechat-icon-img">
            <img src="../../../assets/img/wechat.png" class="wp100" alt="">
            </div>
            <div class="wechat-icon-img">
            <img src="../../../assets/img/qq.png" class="wp100" alt="">
            </div>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>
<script>
export default {
  props: {
    shareParams: {
      type: Object
    }
  },
  methods: {
    closeShare () {
      this.$emit('closeShare')
    }
  },
  created () {
    this.$emit('reConfig')
  }
}
</script>
<style lang="scss" scoped>
  #share{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 50;
    height: 100%;
    background: rgba($color: #000000, $alpha: .75);
    .wechat-cotent{
      width: 100%;
      height: 100%;
      color: #fff;
      font-size: 34px;
      .jiantou{
        width: 206px;
        position: absolute;
        right: 30px;
        top:20px;
      }
      .wechat-icon-img {
        width: 110px;
        height: 110px;
      }
      .step1{
        padding:123px 0 75px 60px;
        display: flex;
        align-items: center;
        align-content: center;
        .dot-img{
          width: 70px;
          margin-left: 20px;
        }
      }
      .step2{
        padding-left: 110px;
        padding-bottom: 60px;
      }
      .wechat-icon{
        width: 484px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
      }
    }
  }
</style>



