<template>
<div v-if="$utils.showNavBar()">
  <div class="height">

  </div>
  <div id="header-nav" class="text-center">
    <div class="left" @click="toPage">
      <img class="closeBtn" v-if="isCurrentPage" src="../../assets/img/close.png" alt="">
      <div class="iconSize" v-else>
        <img src="../../assets/img/black-left.png" alt="">
      </div>
    </div>
    {{title}}
  </div>
</div>
</template>

<script>
export default {
  name: 'header-nav',
  props: ['title', 'isCurrentPage'],
  data () {
    return {

    }
  },
  methods: {
    toPage () {
      if (this.isCurrentPage) {
        this.$emit('closePage')
      } else {
        this.$router.go(-1)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.height{
  height: 90px;
}
#header-nav{
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  line-height: 90px;
  height: 90px;
  background-color: #fff;
  border-bottom: 1px solid #f2f2f2;
  color: #222;
  .left{
    position: absolute;
    padding: 30px 29px;
    line-height: 0;
    .iconSize {
      width: 20px;
      height: 35px;
    }
    img{
      width: 100%;
      height: 100%;
    }
    .closeBtn{
      width: 30px;
      height: 30px;
    }
  }
}
</style>



