<template>
  <div class="recommend_buttom">
    <div class="recommendCar" ref="recommonAdbox">
      <div class="title text-center f30"><span class="left"></span><span class="txt"></span><span class="right"></span></div>
      <div class="pic">
        <template v-for="(item,index) in recommendCar">
          <div class="listPic" :key="index" :class="{'advertise': item.type === 100}">
            <div v-if="item.type === 100">
              <ad-view @closeAd="deleteAd(index)" :adObj="item"></ad-view>
            </div>
            <div v-else @click="toPage(item.level,item.id)">
              <span class="tolevel f24">{{item.level}}</span>
              <div class="toimg"><img :src="item.thumb" alt=""></div>
              <div class="text">
                <div class="f30 toname">{{item.fullname}}</div>
                <div class="flex topage">
                  <span class="redColor"><b>0</b>.00</span>
                  <div class="price">&yen;&nbsp;{{item.price|newMoney}}</div>
                </div>
                <div class="flex gradColor f24 topr"><s></s><span>{{item.sales_num?item.sales_num:0}} </span></div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div class="bottomTitle">
      <!--  -->
      <transition name="">
        <div class="bottomTitle-title f30" v-if="chaS">
          <div class="iconSize">
            <img src="../../assets/img/my/message_active.png" alt="">
          </div>
          <div></div>
          <div class="closeImage" @click="chaS=false"></div>
        </div>
      </transition>
      <div v-if="listDate.vcoin - (listDate.has_vcoin ? listDate.has_vcoin : 0) > 0"  class="bottomTitle-bottom f26"> {{(listDate.vcoin - (listDate.has_vcoin ? listDate.has_vcoin : 0))|newMoney}}  <span class="fx" @click="setShare"></span></div>
      <div v-else class="bottomTitle-bottom f26"> <span class="fx" @click="setShare"></span></div>
    </div>
  </div>
</template>

<script>
import AdView from '@/components/mutiAdList/index'
export default {
  components: {
    AdView
  },
  props: {
    listDate: {
      type: Object
    }
  },
  data () {
    return {
      recommendCar: [],
      vconiV: 0,
      hasVconiV: 0,
      chaS: false,
      isScroll: false
    }
  },
  mounted () {
    this.getRecommend()
    this.vconiV = this.listDate.vcoin
    if (this.listDate.has_vcoin === '') {
      this.hasVconiV = 0
    } else {
      this.hasVconiV = this.listDate.has_vcoin
    }
    this.$nextTick(() => {
      window.removeEventListener('scroll', this.scrollFn)
      window.addEventListener('scroll', this.scrollFn)
    })
  },
  watch: {
    '$route.params.id' () {
      this.getRecommend()
    }
  },
  methods: {
    deleteAd (index) {
      this.recommendCar.splice(index, 1)
    },
    setShare () {
      this.$emit('setShare')
    },
    getRecommend () {
      this.$api.get('RECOMMENDED_AWARDS', {}, res => {
        this.recommendCar = res.data
      })
    },
    toPage (item, id) {
      if (item > 15) {
        this.$router.replace('/car_detail/' + id)
      } else {
        this.$router.replace('/goods_detail/' + id)
      }
    },
    scrollPn () {
      // 
      let ads = this.$refs.recommonAdbox.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    scrollFn () {
      if (this.$utils.scrollHeight() > 20) {
        this.isScroll = true
      }
      if (this.$utils.scrollHeight() <= 0 && this.isScroll) {
        this.chaS = true
        setTimeout(() => {
          this.chaS = false
        }, 3000)
        window.removeEventListener('scroll', this.scrollFn)
      }
    }
  },
  filters: {
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + ''
      } else {
        return num.toFixed(2)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .recommend_buttom{
    padding-bottom: 120px;
    .pic {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
      overflow-x: hidden;
    }
    .recommendCar{
      .title{
        height: 105px;
        line-height: 105px;
        box-sizing: border-box;
        position: relative;
        background: white;
        margin: 20px 0;
        .left,.right{
          display: inline-block;
          width: 100px;
          height: 2px;
          background: #000001;
          position: absolute;
          top: 50.5px;
        }
        .left{
          left: 200px;
        }
        .right{
          right: 200px;
        }
      }
      .listPic{
        width: 367px;
        margin-bottom: 20px;
        position: relative;
        background: white;
        .text{
          padding: 19px 30px 0;
        }
        .toimg{
          /*height: 350px;*/
          img{
            width: 100%;
            /*height: 100%;*/
            vertical-align: middle;
          }
        }
        .tolevel{
          position: absolute;
          top: 0;
          left: 0;
          color: white;
          width: 70px;
          height: 28px;
          box-sizing: border-box;
          padding-left: 4px;
          line-height: 28px;
          background: url('../../assets/img/my/grade.png') no-repeat left top;
          background-size: 100% 100%;
        }
        .toname{
          height: 45px;
          line-height: 45px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow : ellipsis;
        }
        .topage{
          height: 44px;
          display: flex;
          margin-top: 15px;
          justify-content: space-between;
          align-items: flex-end;
          .redColor {
            font-weight: 900;
            b {
              font-size: 50px;
            }
          }
          .price {
            text-decoration: line-through;
            color: #BABABA;
            font-size: 24px;
            line-height: 34px;
          }
        }
        .topr{
          padding: 13px 0 17px;
          line-height: 30px;
          display: flex;
          align-items: flex-end;
          justify-content: space-between;
        }
      }
      .listPic:nth-of-type(2n){
        margin: 0 0 20px 0;
      }
      &:after{
        display:block;clear:both;content:"";visibility:hidden;height:0
      }
    }
    .bottomTitle{
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      .bottomTitle-bottom{
        height: 120px;
        line-height: 120px;
        padding-left: 20px;
        background: white;
        box-shadow: -4px 0 12px rgba(0,0,0,0.1);
        font-size: 26px;
      }
      .fx{
        position: absolute;
        right: 30px;
        bottom: 30px;
        height: 64px;
        line-height: 64px;
        padding: 0 20px;
        display: inline-block;
        min-width: 176px;
        text-align: center;
        box-sizing: border-box;
        background: #E71010;
        color: #fff;
        border-radius: 10px;
      }
      .bottomTitle-title{
        height: 100px;
        line-height: 100px;
        box-sizing: border-box;
        padding: 0 30px;
        background: rgba(0,0,0,0.85);
        margin-bottom: 10px;
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .iconSize{
          width: 30px;
          height: 30px;
          vertical-align: middle;
          display: flex;
        }
        &>div:last-child{
          width: 40px;
          text-align: center;
        }
        .closeImage {
          width: 60px;
          height: 60px;
          background: url('../../assets/img/video-close.png') no-repeat center;
          background-size: 25px 25px;
        }
      }
    }
  }
  .gradColor{
    color: #BABABA;
  }
  .advertise {
    width: 100%!important;
    box-align: border-box;
    padding: 0 30px;
  }
</style>



