<template>
  <div id="loading">
    <div class="isLoading" v-if="loading === 1 && page !== 1">
      <mt-spinner type="snake"></mt-spinner>
      <div class="loadingText">...</div>
    </div>
    <!-- <div class="complete" v-if="loading === 2"></div> -->
  </div>
</template>
<script>
export default {
  props: {
    loading: {
      type: Number
    },
    page: {
      type: Number
    }
  }
}
</script>
<style lang="scss" scoped>
.isLoading {
  padding-top: 10px;
}
.complete {
  text-align: center;
  font-size: 30px;
  line-height: 60px;
  color: #999;
}
</style>



