<template>
  <div id="exit" class="maskBox">
    <div class="confirmBox">
      <div class="flex-row title">{{configParams.title ? configParams.title :',?'}}</div>
      <div class="buttonGroup flex-row jusB">
        <div class="cancel flex-row" @click="close('cancel')">{{configParams.cancelText ? configParams.cancelText: ''}}</div>
        <div class="doTask flex-row" @click="close('confirm')">{{configParams.confirmText ? configParams.confirmText: ''}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    carInfo: {
      type: Object
    },
    config: {
      type: Object
    } // {title, confirmText, cancelText}
  },
  data () {
    return {
      configParams: {}
    }
  },
  methods: {
    close (action) {
      this.$emit('closeConfirm', {action})
    }
  },
  created () {
    this.configParams = this.config ? JSON.parse(JSON.stringify(this.config)) : {}
  }
}
</script>
<style lang="scss" scoped>
#exit{
  .confirmBox{
    width: 600px;
    border-radius: 15px;
    overflow: hidden;
    color: #232323;
    font-size: 30px;
    &::after{
      display: none
    }
    .title{
      height: 160px;
      font-size: 32px;
      line-height: 42px;
      width: 500px;
      margin: 0 auto;
      padding: 20px 0;
    }
    .text-tip{
      width: 530px;
      height: 84px;
      margin: 40px auto 20px;
      text-align: center;
      line-height: 36px;
    }
    .carImgbox{
      background: #D8D8D8;
      width: 500px;
      height: 240px;
      margin: 0 auto;
      overflow: hidden;
    }
    .carImage{
      height: 187px;
      width: 250px;
    }
    .buttonGroup{
      height: 90px;
      >div{
        flex: 1;
        height: 90px;
        box-sizing: border-box;
      }
      .cancel{
        color: #232323;
        border-top: 1px solid #ddd;
        box-sizing: border-box;
      }
      .doTask{
        background: #FFAC00;
        color: #fff;
      }
    }
  }
}
</style>



